<script>
import axios from 'axios'
export default {
    data() {
        return {
            city: 'Tashkent',
            error: '',
            input: false,
            week: false,
            api: '19f75387390aefa6f9d138ee8857d6c8',
            info: {},
            lat: null,
            lon: null,
            data: null,
            minutes: new Date().getMinutes(),
            temp: null,
            feels: null,
            daily: null,
            day: null,
            night: null,
            nextSevenDays: null,
        }
    },
    methods: {
        async getWeather() {
            if (this.city.trim().length < 2) {
                this.error = 'Введите правильное название города'
                alert(this.error)
                return false
            }
            this.error = ''
            try {
                const res = await axios.get(`https://api.openweathermap.org/geo/1.0/direct?q=${this.city}&appid=${this.api}&units=metric`);
                this.lat = res.data[0].lat
                this.lon = res.data[0].lon

                const res2 = await axios.get(`https://api.openweathermap.org/data/2.8/onecall?lat=${this.lat}&lon=${this.lon}&exclude=minutely,hourly,alerts&appid=${this.api}&units=metric&lang=ru`);
                this.data = res2.data
                this.temp = Math.round(this.data.current.temp)
                this.feels = Math.round(this.data.current.feels_like)

                console.log(this.data);
            } catch (error) {
                console.error('Error fetching weather data:', error);
                // this.info = null; 
            }
        },
        async getWeather2() {
            try {
                const res3 = await axios.get(`https://api.openweathermap.org/data/2.8/onecall?lat=${this.lat}&lon=${this.lon}&exclude=minutely,hourly,alerts&appid=${this.api}&units=metric&lang=ru`);
                this.daily = res3.data.daily;
                this.day = Math.round(this.daily[0].feels_like.day);
                this.night = Math.round(this.daily[0].feels_like.night);

                const currentDayOfWeek = new Date().getDay();
                const daysOfWeek = ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'];

                const nextSevenDays = Array.from({ length: 7 }, (_, index) => {
                    const dayIndex = (currentDayOfWeek + index) % 7;
                    return daysOfWeek[dayIndex];
                });
                this.nextSevenDays = nextSevenDays;

            } catch (error) {
                console.error('Ошибка при получении данных о ежедневной погоде:', error);
            }
        },
        getFormattedDate(dt, type) {
            const milliseconds = dt * 1000;
            const data = new Date(milliseconds);

            let result =
                type === 'month' ? data.toLocaleString('ru-RU', { month: 'short' }) :
                    type === 'day' ? `${data.getDate()} ${data.toLocaleString('ru-RU', { month: 'short' })}` : '';

            return result;
        },

    },
    created() {
        this.getWeather();
    }
}
</script>

